@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a1323d}

.matchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.matchDetailCard .match-date,.matchDetailCard .match-venue,.matchDetailCard .match-result{margin-top:10px}.matchDetailCard .additional-detail{text-align:right}.matchDetailCard .additional-detail>p{margin-bottom:20px}

.matchSmallCard{padding:10px;margin-bottom:20px}.matchSmallCard .match-result{margin-top:10px}

.teamPage{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(3, 1fr);grid-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}.teamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:flex-end}.teamPage .team-name{font-size:4rem;margin-bottom:20px}.teamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.teamPage .match-detail-section{grid-column:span 4}.teamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}

.yearSelector{text-align:right;list-style-type:none;grid-column-gap:20px;column-gap:20px}.yearSelector li{margin:10px 0px}

.matchPage{display:grid;grid-template-columns:1fr 5fr}.matchPage .year-selector{justify-self:flex-end;padding:20px}.matchPage .page-heading{margin:20px 0px}

.homePage .app-name{font-size:3rem;margin:30px auto;text-align:center}.homePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}

.teamTile{height:170px;text-align:center;border:1px solid white;padding:20px}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

